index: 999;\n  }\n}","#login {\n  bottom: 0;\n  display: table;\n  height: auto;\n  left: 0;\n  margin: auto;\n  position: absolute;\n  right: 0;\n  top: 0;\n  width: $login;\n  form {\n    background: #FFF;\n    border: 1px solid $line;\n    border-radius: $radius;\n    padding: 1.5*$space/2;\n    h1 {\n      border-bottom: 1px solid $line;\n      font-size: inherit;\n      margin: -1.5*$space/2;\n      margin-bottom: $space/2;\n      padding: 1.5*$space/2;\n    }\n    input,\n    .button {\n      width: 100%;\n    }\n  }\n}","#header {\n  border-bottom: 1px solid $line;\n  @include clearfix;\n  margin-bottom: $space/2;\n  padding: $space/2 0;\n  a {\n    color: inherit;\n    &:hover {\n      color: $primary;\n      text-decoration: none;\n    }\n  }\n  h1 {\n    font-size: inherit;\n    margin: 0;\n  }\n  .links {\n    float: right;\n    line-height: 1;\n    a {\n      float: left;\n      margin-left: $space;\n      img {\n        border-radius: $space;\n        float: left;\n        margin-right: $space/4;\n        width: $space;\n      }\n    }\n  }\n}\n",".notification {\n  border-radius: $radius;\n  color: #FFF;\n  display: block;\n  padding: $space/4 $space/2;\n  &.info { background: $red; }\n  &.info { background: $blue; }\n  &.success { background: $green; }\n  &.undefined { background: $purple; }\n  &.warning { background: $orange; }\n  > *:last-child {\n    margin-bottom: 0;\n  }\n  a {\n    color: #FFF;\n    text-decoration: underline;\n    &:active,\n    &:hover {\n      text-decoration: none;\n    }\n  }\n  .close {\n    background: transparent;\n    border: 0;\n    color: inherit;\n    float: right;\n    opacity: 0.5;\n    padding: 0;\n    text-decoration: none;\n    transition-property: (opacity);\n    transition-duration: ($ease);\n    &:hover {\n      opacity: 1;\n    }\n  }\n}","#main {\n  .wrapper > .header {\n    display: none;\n  }\n  .item {\n    background: #FFF;\n    border-radius: $radius;\n    border: 1px solid $line;\n    margin-bottom: $